.filters-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 5rem;
    flex-wrap: wrap;

    .search-container{
        display: flex;
        align-items: center;

        img{
            margin-right: -45px;
            z-index: 1;
            width: 20px;     
        }
        
        input{
            width: 30rem;
            height: 4rem;
            border-radius: 5px;
            border: none;
            padding: 1rem 4rem;
            font-size: 15px;
            font-weight: 500;
        }

        input:focus-visible{
            outline: none;
        }
    }

    select{
        width: 15rem;
        height: 4rem;
        border: none;
        border-radius: 5px;  
        padding: 1rem 2rem; 
        font-size: 16px;
        cursor: pointer;

        option{
            font-size: 16px;
        }
    }

    select:focus-visible{
        outline: none;
    }
}

.searchImgLight{
    filter:  brightness(300%);
}

.searchImgDark{
    filter:  brightness(0%);
}